import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import clock from './clock.png';
import Form from 'react-bootstrap/Form';
import { GetStatus, CallMatrix, MatrixPost } from './util';

class Clock extends React.Component {
    constructor(props) {
        super(props);
        this.state = { "enabled": false };
    }
    componentDidMount() {
        this.updateStatus()
    }
    updateStatus() {
        GetStatus(`clock/status`, (val) => {
            this.setState({
                "enabled": val,
            })
        })
    }

    handleSwitch = (apiOn, apiOff, stateVar) => {
        var currentState = this.state[stateVar]
        console.log("handle switch", currentState)
        if (currentState) {
            console.log("Turn off", apiOff)
            CallMatrix(apiOff);
        } else {
            console.log("Turn on", apiOn)
            CallMatrix(apiOn);
        }
        this.setState(prev => ({
            [stateVar]: !prev[stateVar],
        }))
    }

    handleJump = (board) => {
        MatrixPost("jump", `{"board":"${board}"}`)
        this.updateStatus()
    }
    render() {
        return (
            <Container fluid>
                <Row className="text-center"><Col><Image src={clock} style={{ height: '100px', width: 'auto' }} onClick={() => this.handleJump("clock")} fluid /></Col></Row>
                <Row className="text-left">
                    <Col>
                        <Form.Switch id="clockenabler" label="Enable/Disable" checked={this.state.enabled}
                            onChange={() => this.handleSwitch(`clock/enable`, `clock/disable`, "enabled")} />
                    </Col>
                </Row>
                <Row className="text-left">
                    <Col>
                        <Button variant="primary" onClick={() => this.handleJump("clock")}>Jump</Button>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default Clock;